.close {
    position: absolute;
    top: 47px;
    right: 56px;
    cursor: pointer;

    &:hover {
        opacity: .7;
    }
}

.ic_close {
    width: 16px;
    height: 16px;
}

.logo {
    font-size: 40px;
    font-style: normal;
    font-weight: 800;
    line-height: 48px;
    text-align: center;
    display: block;
    color: #fff;
}

.logo-sm {
    font-size: 30px;
    font-style: normal;
    font-weight: 800;
    line-height: 36px;
    letter-spacing: 0em;
    display: block;
    text-align: left;
    color: #fff;
    margin-bottom: 56px;
    margin-top: -40px;
}

.main-label {
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: 30px;
    letter-spacing: 0em;
    text-align: left;
    color: #fff;
    margin-bottom: 20px;
}


.select__body {
    display: none;
    left: 0;
    position: absolute;
    right: 0;
    top: 95%;
    z-index: 30;
    background: #fff;
    box-shadow: 0 10px 20px #0000000a;
    border-radius: 0 0 10px 10px;

}

.select__item {
    color: #000;
    font-size: 18px;
    letter-spacing: 0em;
    text-align: left;
    padding: 20px 18px;
    font-weight: 400;
    display: flex;
    align-items: center;
    border-bottom: 1px solid #ececec;
    cursor: pointer;
    background: #fff;

    &:hover {
        background: #ececec;
    }

    &:last-of-type {
        border-bottom: 0;
    }
}



.select {
    border-radius: 10px;
    border: 1px solid #ffffff80;
    width: 49%;
    color: #fff;
    padding-left: 20px;
    padding-right: 20px;
    font-size: 17px;
    font-style: normal;
    font-weight: 700;
    letter-spacing: 0em;
    text-align: left;
    margin-bottom: 30px;
    position: relative;
}

.select__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 55px;
}

.select__current {
    display: flex;
    align-items: center;
}

.flag {
    padding-right: 15px;
}

.footer {
    display: flex;
    align-items: center;
}

.btn-buy {
    border: none;
    box-shadow: none;
    color: #fff;
    font-size: 17px;
    font-style: normal;
    font-weight: 700;
    letter-spacing: 0.03em;
    text-align: center;
    text-transform: uppercase;
    padding: 18px 75px;
    text-transform: uppercase;
    border-radius: 30px;
    cursor: pointer;
    margin-right: 32px;
}

.ic_header {
    width: 11px;
    height: 15px;
    margin-right: 12px;
}

.footer_links {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
}

.footer_links a {
    margin-right: 30px;
    color: #fff;
    font-style: normal;
    font-weight: normal;
    font-size: 15px;
    line-height: 136.85%;
    text-decoration-line: underline;
    display: flex;
    align-items: center;


    &:hover {
        opacity: .7;
    }
}

.links_wrp {
    display: flex;
    flex-wrap: wrap;
}

@media only screen and (max-width : 992px) {
    .select {
        width: 100%;
        margin-bottom: 20px;

    }

    .logo-sm {
        margin-bottom: 26px;
    }

    .list_lnk {
        padding-left: 20px;
    }

    .main-label {
        margin-bottom: 10px;
    }

}

@media only screen and (max-width : 768px) {
    .list_lnk {
        margin-top: 17px;
    }

}

@media only screen and (max-width : 552px) {
    .logos-wrp {
        display: flex;
        flex-wrap: wrap;
    }

    .logo-sm {

        margin-top: 0px;
        order: 1;
        flex-basis: 100%;
        margin-bottom: 10px;
    }

    .logo {
        flex-basis: 100%;
        order: 2;
        text-align: left;
        margin-bottom: 10px;
        font-size: 25px;
    }

    .main-label {
        font-size: 18px;
    }

    .footer_links a {
        align-items: flex-start;
        flex-basis: 100%;
    }

    .footer {
        flex-wrap: wrap;
    }

    .btn-buy {
        width: 100%;
        margin-bottom: 30px;
        margin-right: 0;
    }

    .list_lnk {
        padding-left: 23px;

    }

    .close {
        top: 27px;
        right: 31px;

    }

}
