[type="checkbox"]:checked,
[type="checkbox"]:not(:checked),
[type="radio"]:checked,
[type="radio"]:not(:checked) {
    position: absolute;
    left: -9999px;
    width: 0;
    height: 0;
    visibility: hidden;
}

.cb-tools {
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.input-wrp {
    flex-basis: 23.7%;
    display: inline-flex;
    border-radius: 10px;
    background: #fff;
    margin-bottom: 60px;
}


.checkbox-tools:checked + label,
.checkbox-tools:not(:checked) + label {
    position: relative;
    display: inline-block;
    padding: 25px 26px 36px 26px;
    width: 100%;
    text-align: center;
    border-radius: 10px;
    overflow: hidden;
    cursor: pointer;
    -webkit-transition: all 300ms linear;
    transition: all 300ms linear;
}

.checkbox-tools:not(:checked) + label {
    background-color: #fff;
}

.checkbox-tools:checked + label {
    background-color: #fff;

}

.label-title {
    font-size: 30px;
    font-style: normal;
    font-weight: 700;
    line-height: 36px;
    letter-spacing: 0em;
    text-align: center;
    display: block;
    color: #737373;
    font-feature-settings: 'pnum'on, 'lnum'on;
    text-transform: initial;
    margin-bottom: 21px;
    padding: 3px 0;

}

.label-logo {
    display: block;
    text-align: center;
    margin-bottom: 20px;
}

.label-date {
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    font-feature-settings: 'pnum'on, 'lnum'on;
    text-transform: initial;
    color: #7D7D7D;
    display: block;
    text-align: center;
    letter-spacing: 0em;
    margin-bottom: 15px;
}

.label-summ {
    font-style: normal;
    font-weight: bold;
    font-size: 30px;
    line-height: 120.9%;
    text-align: center;
    font-feature-settings: 'pnum'on, 'lnum'on;
    color: #737373;
    text-transform: initial;
}

@media only screen and (max-width : 992px) {
    .cb-tools {
        flex-wrap: wrap;
        margin-top: 10px;
    }

    .input-wrp {
        flex-basis: 48.5%;
        margin-bottom: 20px;


    }

    .footer {
        margin-top: 10px;
    }
}


@media only screen and (max-width : 552px) {
    .input-wrp {
        flex-basis: 100%;
        margin-bottom: 10px;
    }

    .label-title {
        font-size: 20px;
        line-height: 24px;
    }

    .checkbox-tools:checked + label,
    .checkbox-tools:not(:checked) + label {

        padding: 18px 26px 20px 18px;
    }

    .label-logo img {
        max-width: 27%;
    }

    .label-date {
        font-size: 15px;
        font-weight: 500;
        line-height: 23px;
    }

    .label-summ {
        font-size: 20px;
    }


}
